import React from 'react';
import { Link } from 'react-router-dom';

import homeIcon from '../static/home.svg';
import techIcon from '../static/tech.svg';
import contactIcon from '../static/contact.svg';
import './navigation.css';

const links = [
  {
    label: 'Home',
    icon: homeIcon,
    destination: '/',
  },
  {
    label: 'Technologies',
    icon: techIcon,
    destination: '/technologies',
  },
  {
    label: 'Contact me',
    icon: contactIcon,
    destination: '/contact',
  },
];

const NavBar = ({ header, subText }) => {
  const headerLetters = header.split('');
  const firstLetter = headerLetters.shift();

  return (
    <nav className="nav-container">
      <div className="nav-header">
        <h2 className="name">
          <span className="jay">{firstLetter}</span>
          {headerLetters.join('')}
          <span className="location">{subText}</span>
        </h2>
      </div>
      <div className="nav-links">
        {links.map(({ label, icon, destination }) => (
          <Link to={destination}>
            <img src={icon} className="nav-icon" alt={label} />
            <span className="nav-label">{label}</span>
          </Link>
        ))}
      </div>
    </nav>
  );
};

export default NavBar;
