import React from 'react';
import PropTypes from 'prop-types';

import greenCheckIcon from '../static/green-checkmark.svg';
import redCheckIcon from '../static/red-x.svg';
import './notification.css';

const Notification = ({ isSent }) => {
  const messageText = isSent ? 'Message sent!' : 'Oops not sent!';
  return (
    <div className="notification">
      <div className="model-background"></div>
      <div className="model-card">
        {messageText}
        <img className="check-svg" src={isSent ? greenCheckIcon : redCheckIcon} alt="sent-status" />
      </div>
    </div>
  );
};

Notification.propTypes = {
  messageText: PropTypes.string.isRequired,
  isSent: PropTypes.bool,
};

Notification.defaultProps = {
  isSent: undefined,
};

export default Notification;
